import { ChangeDetectionStrategy, Component, inject } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Panel } from 'primeng/panel';
import { InputTextModule } from 'primeng/inputtext';
import { FormBuilder, ReactiveFormsModule, Validators } from '@angular/forms';
import { ButtonModule } from 'primeng/button';
import { AuthService } from './services/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'lab-auth',
  imports: [CommonModule, Panel,InputTextModule, ReactiveFormsModule,ButtonModule],
  template: `
    <div class="flex justify-center items-center h-screen">
    <p-panel header="Login" class="bg-white rounded-lg p-6 w-96">
        <form class="space-y-4" [formGroup]="logingForm" (ngSubmit)="submitForm()">
            <div>
                <label for="email" class="block text-sm font-medium text-gray-700">Email</label>
                <input 
                    formControlName="username"
                    id="email" 
                    type="email" 
                    pInputText 
                    class="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm" 
                    placeholder="Enter your email" 
                />
            </div>
            <div>
                <label for="password" class="block text-sm font-medium text-gray-700">Password</label>
                <input 
                    formControlName="password"
                    id="password" 
                    type="password" 
                    pInputText 
                    class="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm" 
                    placeholder="Enter your password" 
                />
            </div>
            <div>
                <button 
                    pButton 
                    type="submit" 
                    label="Login" 
                    class="w-full font-medium py-2 px-4 rounded-md">
                </button>
            </div>
        </form>
    </p-panel>
</div>
  `,
  styleUrl: './auth.component.css',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AuthComponent {
  private _fb = inject(FormBuilder);
  private _authService = inject(AuthService);
  private _router = inject(Router)
  logingForm = this._fb.group({
    username: ['', [Validators.required]],
    password: ['', [Validators.required]],
  });

  submitForm(){
    this._authService.login(this.logingForm.value).subscribe(data => {
      this._router.navigateByUrl('main')
    })
  }
}
